
class ProfileModel {

    static SEXE_MALE = "male";
    static SEXE_FEMALE = "female";
    static STATUS_PREGNANT = "pregnant";

    constructor(id, sexe, weight, age, height, nap) {
        this.id = id;
        this.sexe = sexe;
        this.weight = weight; // kg
        this.age = age; // years
        this.height = height; // cm
        this.accountId = "";
        this.status = "";
        this.physicalActivityLevel = nap;
    }
}

export default ProfileModel;


