import ProfileModel from '../models/ProfileModel';
import * as common from '../util/common';

class EnergyProcessor {

    /**
     * Formule basée sur l'équation de Black et al. (1996)
     * @param {*} profileModel 
     */
    static calculateBasicMetabolisme(profileModel) {
        const MB_CONST_191 = 191;
        const MB_WEIGHT_COEFFICIENT = 0.48;
        const MB_HEIGHT_COEFFICIENT = 0.50;
        const MB_AGE_COEFFICIENT = -0.13;

        let subResult = Math.pow(profileModel.weight, MB_WEIGHT_COEFFICIENT) *
            Math.pow(common.convertCentimetersToMeters(profileModel.height), MB_HEIGHT_COEFFICIENT) *
            Math.pow(profileModel.age, MB_AGE_COEFFICIENT);

        const pregnant = ProfileModel.STATUS_PREGNANT === profileModel.status;
        const particularCases = pregnant || this.checkTeenager(profileModel.age);
        let mb = null;

        if (ProfileModel.SEXE_MALE === profileModel.sexe) {
            mb = this.calculateBasicMetabolismeForMale(subResult, particularCases);
        }
        mb = this.calculateBasicMetabolismeForFemale(subResult, particularCases);
        return particularCases ? Math.round(mb) : Math.round(mb * MB_CONST_191);
    }

    static calculateBasicMetabolismeForMale(subResult, particularCases) {
        const MB_CONST_1_DOT_083 = 1.083;
        if (particularCases) {
            return this.calculateBasicMetabolismeForParticularCases(subResult) * MB_CONST_1_DOT_083;
        }
        return subResult * MB_CONST_1_DOT_083;
    }

    static calculateBasicMetabolismeForFemale(subResult, particularCases) {
        const MB_CONST_0_DOT_963 = 0.963;
        if (particularCases) {
            return this.calculateBasicMetabolismeForParticularCases(subResult) * MB_CONST_0_DOT_963;
        }
        return subResult * MB_CONST_0_DOT_963;
    }

    static calculateBasicMetabolismeForParticularCases(subResult) {
        const MB_CONST_239 = 239;
        return subResult * MB_CONST_239;
    }

    static checkTeenager(age) {
        return age >= 13 && age <= 19;
    }

    static getSedentaryNAP(age) {
        return this.checkTeenager(age) ? 1.8 : 1.4;
    }

    static getActiveNAP(age) {
        return this.checkTeenager(age) ? 2 : 1.6;
    }

    static getAthleticNAP(age) {
        return this.checkTeenager(age) ? 2.2 : 1.7;
    }

    static calculateDailyEnergicCost(mb, nap) {
        return Math.round(mb * nap);
    }
}

export default EnergyProcessor;


