import React, { Component } from 'react';
import View from './View';
import ProfileEditor from './ProfilEditor';
import ProfileModel from './models/ProfileModel';
import EnergyProcessor from './process/EnergyProcessor';

class ProfilView extends Component {

    constructor(props) {
        super(props);


        this.profilModel = new ProfileModel(0, ProfileModel.SEXE_MALE, 120, 20, 185);
        this.profilModel.physicalActivityLevel = EnergyProcessor.getSedentaryNAP(this.profilModel.age);
        this.mb = EnergyProcessor.calculateBasicMetabolisme(this.profilModel);
        this.dej = EnergyProcessor.calculateDailyEnergicCost(this.mb, this.profilModel.physicalActivityLevel);
        this.state = {

        }
    }

    render() {
        return (
            <View tabId={this.props.tabId} active={this.props.active}>
                <ProfileEditor />
                <h1>Profil</h1>

                <h2>Mensuration</h2>
                <p>Poids : <strong>{this.profilModel.weight}</strong></p>
                <p>Taille : <strong>{this.profilModel.height}</strong></p>
                <p>Age : <strong>{this.profilModel.age}</strong></p>
                <p>Sexe : <strong>{this.profilModel.sexe}</strong></p>


                <h2>Profil energitique</h2>
                <p>Metabolisme de base (MB): {this.mb} kcal</p>
                <p>Niveau d'activité phisique (NAP):{this.profilModel.physicalActivityLevel}</p>
                <p>Dépenses energitique journaliere (DEJ): {this.dej} kcal</p>
                <p>Apport calorique actuel : ??? kcal</p>



            </View>
        );
    }
}
export default ProfilView;



